.login--container {
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-column-gap: 0px;
  place-self: center;
  background-color: #ffffff;

  a {
    border-bottom: 1px solid;
    text-decoration: none;
    padding: 2px 1px 0;
  }

  a:link {
    color: #34018E;
  }

  a:visited {
    color: #01438e;
  }

  a:focus {
    border-bottom: 1px solid;
  }

  a:hover {
    color: #2705ff;
  }

  a:active {
    color: rgba(24, 172, 204, 0.74);
  }


  .login--logo {
    background-color: #34018E;
    display: flex;
    flex-direction: column;
    justify-content: center;
    row-gap: 100px;
    align-items: center;
    text-align: center;
    min-height: 100vh;
    width: 50vw;

    .login--logo--text {
      color: #FFFFFF;
      font-size: 35px;
    }

  }

  .login--form {
    display: grid;
    grid-template-columns: 1fr;
    grid-auto-rows: minmax(min-content, max-content);
    row-gap: 6em;
    min-height: 100vh;
    width: 50vw;

    background: linear-gradient(180.51deg, rgba(62, 228, 231, 0.3) -61.93%, rgba(52, 1, 142, 0.3) -34.49%, rgba(255, 255, 255, 0) 23.05%, rgba(213, 189, 255, 0.3) 75.25%, rgba(62, 228, 231, 0.3) 112.56%);

    .login--form--header {
      width: 90%;
      margin: 30px 0 50px;
      text-align: right;
      justify-self: center;

      a {
        border-bottom: 0px ;
        text-decoration: none;
        padding: 2px 1px 0;

        .login--arrow {
          position: relative;
          top: 8px;
          left: 8px;
        }
      }
    }

    .login--form--body {
      width: 50%;
      justify-self: center;
      display: flex;
      flex-direction: column;
      justify-content: center;
      align-items: center;
      align-content: space-around;
      text-align: center;

      .login--form--socials {
        display: flex;
        flex-direction: row;
        justify-content: center;
        align-items: center;
        text-align: center;
        width: 100%;
      }

      .login--form--line--wrapper {
        display: flex;
        flex-direction: row;
        justify-content: space-between;
        align-items: center;
        text-align: center;
        width: 100%;

        .login--form--line {
          display: block;
          width: 30%;
          height: 2px;
          background-color: black;
          transform: translate(0px, 0px);
        }
      }

      .login-form-inputs {
        display: flex;
        flex-direction: row;
        justify-content: space-evenly;
        align-items: center;
        text-align: center;
        width: 100%;

        .forgot--password {
          width: 95%;
          text-align: right;
          padding: 10px 10px;

        }
      }



    }
  }
}