.main-layout {
    display: grid;
    grid-template-columns:
        minmax(2rem, 1fr)
        minmax(auto, $normal-breakpoint)
        minmax(2rem, 1fr);

    > * {
        grid-column: 2;
        &.full {
            grid-column: 1 / -1;
        }
        .app-navbar-wrapper {
            grid-column: 1 / -1;
        }
        .app-navbar {
            grid-column: 2;
        }
        .footer__bg {
            grid-column: 2;
            grid-row: 3/4;
            // position: absolute;
            // bottom: 0;
            // position: sticky;
        }
        .footer__container {
            grid-column: 2/3;
        }
    }
    @include for-narrow-layout {
        grid-template-columns:
            minmax(2rem, 1fr)
            minmax(auto, $mobile-breakpoint)
            minmax(2rem, 1fr);
    }
    @include for-medium-layout {
        grid-template-columns:
            minmax(2rem, 1fr)
            minmax(auto, $narrow-breakpoint)
            minmax(2rem, 1fr);
    }
    @include for-normal-layout {
        grid-template-columns:
            minmax(2rem, 1fr)
            minmax(auto, $medium-breakpoint)
            minmax(2rem, 1fr);
    }
    @include for-wide-layout {
        grid-template-columns:
            minmax(2rem, 1fr)
            minmax(auto, $normal-breakpoint)
            minmax(2rem, 1fr);
    }
}
