.task-list {
    direction: rtl;
    width: 100%;
min-height: 60vh;
    .actions-task {
        display: flex;
        justify-content: space-between;
        padding: 0 15px 0 15px;
        font-weight: bold;

        #add-new-task {
            display: flex;
            align-items: center;
            gap: 10px;
        }

        #change-view {
            display: flex;
            align-items: center;
            gap: 10px;
        }
    }

    .projects-row {
        width: 100%;

        .row-item {
            padding-top: 5px;
            padding-bottom: 5px;
            margin-left: 10px;
            margin-right: 10px;

            .row-item:first-child {
                width: 0;
            }
        }

    }

}

.projects-table {
    width: 100%;
    border-spacing: 8px 10px;
    background-color: white;

    .projects-row {
        font-size: 15px;

        .ppl-assigned {
            display: inline-block;
            clear: left;
            max-width: 200px;

            img {
                width: 2.5em;
            }
        }

        td:first-child,
        td:last-child {
            background: none;
        }

        #precentTd {
            border-radius: 20px;
            display: flex;
            width: 100%;
            align-items: center;
            margin-top: 15px;

            span {
                border-radius: 20px;
                text-align: center;
                padding: 3px;
                color: rgba(83, 83, 83, 0.8)
            }
        }

        td {

            background-color: #F2F2F2;
            text-align: center;
        }

    }
}