.sidebarList {
    height: 100%;
    width: 100%;
    padding: 0;
//    padding: 15px;
    // background-color: blue;
    // padding: 25px;
     position: sticky;
     bottom: auto;
    

    .row {
        width: 100%;
        height: 50px;
        list-style-type: none;
        display: flex;
        justify-content: flex-end;
        align-items: center;
        font-family: Rubik-regular;
        padding: 2px;
        white-space: nowrap;
      
    }

}
.row:hover {
    box-sizing: border-box;
    background-color: #f2f2f2;
    border-radius: 0 35px 35px 0;
    cursor: pointer;
    width: 100%;
    // padding: 5px;
    
}
#active{
    background-color: #f2f2f2;
}

.sidebar-admin-right-arrow{
    display: flex;
    justify-content: flex-start;
    flex-direction: column;
    border-left: 2px solid #34018E;
   

    label{
        white-space: nowrap;
    }
    }
