.sidbar-left-arrow{
    height: 100%;
    width: 25px;
    background-color:#35018e3a ;
    position: absolute;
    // right: 0;
    display: flex;
    flex-direction: row-reverse;
    justify-content: center;
   

}
.menu-toggle{
    display: flex;
    align-items: center;
    gap: 5px;
}